   <el-radio-button label="en-US">English</el-radio-button>
  <el-radio-button label="zh-CN" >中文</el-radio-button>   
  </el-radio-group>


  </div>
<div  style="width:1000px;float:left;margin:10px;padding:5px;border:1px solid #D6D6D6">
  <el-form :model="form" :rules="rules" ref="form" label-position="left" class="el-form--label-top">
       <el-row type="flex"   class="row-bg" style="margin:10px">
         
      </el-row>
   <el-row type="flex"   class="row-bg">
         <el-form-item label=""    prop="dbtype">  <span  class="tlabel" >{{getlan('dbtype')}}</span>
       <el-select style="margin-left:5px"  v-model="form.dbtype"  @change="selectdbtype(form.dbtype)" :placeholder="$t('lang.Select')">
         <el-option label="postgres" value="postgres"> </el-option>
         <el-option label="mysql" value="mysql"> </el-option>
         <el-option label="mssql" value="mssql"> </el-option>
          <el-option label="etcd" value="etcd"> </el-option>
        
       </el-select>
             
      </el-form-item>
         
         </el-row>
        <el-row type="flex"   class="row-bg">
     
          <el-form-item label=""    prop="ip">  <span  class="tlabel" >{{getlan('ip')}}</span> <el-input   v-model="form.ip" auto-complete="off" placeholder=""></el-input> </el-form-item>
          <el-form-item label="" prop="port"> <span  class="tlabel">{{getlan('port')}}</span> <el-input  v-model="form.port" auto-complete="off" placeholder=""></el-input> </el-form-item>
       
          <el-form-item label=""    prop="username">  <span  class="tlabel" >{{getlan('username')}}</span> <el-input   v-model="form.username" auto-complete="off" placeholder=""></el-input> </el-form-item>
          <el-form-item label="" prop="password"> <span  class="tlabel">{{getlan('password')}}</span> <el-input  type="password"  v-model="form.password" auto-complete="off" placeholder=""></el-input> </el-form-item>
         </el-row>
       
        <el-row type="flex"   class="row-bg">
          <el-form-item label=""    prop="sshHost">  <span  class="tlabel" >{{getlan('sshHost')}}</span> <el-input   v-model="form.sshHost" auto-complete="off" placeholder="option"></el-input> </el-form-item>
          <el-form-item label="" prop="sshPort"> <span  class="tlabel">{{getlan('sshPort')}}</span> <el-input    v-model="form.sshPort" auto-complete="off" placeholder="option"></el-input> </el-form-item>
           <el-form-item label="" prop="sshUser"> <span  class="tlabel">{{getlan('sshUser')}}</span> <el-input   v-model="form.sshUser" auto-complete="off" placeholder="option"></el-input> </el-form-item>
            <el-form-item label="" prop="sshPass"> <span  class="tlabel">{{getlan('sshPass')}}</span> <el-input  type="password"  v-model="form.sshPass" auto-complete="off" placeholder="option"></el-input> </el-form-item>
         </el-row>
         
 <el-row type="flex"   class="row-bg">
    
<el-button style="width:200px;float:right;margin-left:150px" type="primary" @click="testconnect()">{{getlan('Connect')}}</el-button>
<el-button style="width:200px;float:right;margin-left:150px" type="primary" @click="demo()">{{getlan('Demo')}}</el-button>

 </el-row>
        
        </el-form>

        </div>

<div style="width:100%;float:left;margin-top:10px">

  <!--el-button   style="float:left;margin-left:50px"  @click="showinvoice()"> 进销存网络版 Invoice</el-button>
   <el-button   style="float:left;margin-left:50px"  @click="showinvoice2()"> 进销存单机版 Invoice</el-button-->
  
</div>
  <div class="demonstration h_title"><font>  {{this.errorMessage}}</font></div>
     </el-main>

</template> 

<script>  
import Vue from 'vue'
import Qs from 'qs'
import {Message,Loading} from 'element-ui'

 import{GetPrintData,AddData,GetList,openurl} from "@/api/index"
  import{getDay,getDateTime,getReqUrl,getBrowerName,getLang,getValue,setValue,isInAPP} from "@/utils/pub"
  
       import{ConnectDatabase} from "@/api/httpbusiness"
import VueHtmlToPaper from 'vue-html-to-paper';
import VueCookies from 'vue-cookies'
     import{isInteger,isIP} from "@/utils/validator"
      let loading 

export default {  
  name: 'app',  
  data() {  
    return {  
      errorMessage:'',
       fileList: [], //上传
       form:{
         hiddenSetDb:false,
         userid:getValue("userid"),
   table:'connectdb',
ip:'',
port:'',
username:'',
password:'',
dbname:'',
dbtype:'',
sshHost:'',
sshPort:'',
sshUser:'',
sshPass:'',
              },  
             rules:{
               ip:[
                  { required: true, message: 'Please input ip', trigger: 'blur' },
                   { validator: isIP, message: 'ip wrong',  trigger: 'blur' }
                ], port:[
                  { required: true, message: 'Please input port', trigger: 'blur' },
                    { validator: isInteger, message: 'port wrong',  trigger: 'blur' }
                ],
                
              },
      browername:'',
       tabPosition: 'top',
           tabproPosition: 'top',
      id:'',
      message:'',
      printstr:'',
      sudokus:[],
    
            curSelect:null
    };  
  },components:{
    
  }, mounted:function() 
  {
     //Vue的方法给原生调用，则需要把方法挂在Window下面
    window.gotopage = this.gotopage;
  }, 
        created(){

       if (isInAPP()){
this.hiddenSetDb=false;
       }else{
this.hiddenSetDb=true;
       }

           if(window.location.host.indexOf(":")>0){
    
          this.ip= window.location.host.split(":")[0]
          this.port= window.location.host.split(":")[1]
        }

         if ( this.ip!=null&& this.ip!=undefined&&this.ip!='null'){
            //setValue('ip', this.ip);
            //setValue('port', this.port);
         }

     this.browername= navigator.userAgent;
     var bname=getBrowerName();
           setValue("BrowerName",bname);
        if (getValue("dbip")!=undefined){  this.form.ip=getValue("dbip"); }
        if (getValue("dbport")!=undefined){  this.form.port=getValue("dbport"); }
        if (getValue("dbusername")!=undefined){  this.form.username=getValue("dbusername"); }
        if (getValue("dbtype")!=undefined){
           var dbtype=getValue("dbtype");
           if(dbtype!="sqlite"){ this.form.dbtype=getValue("dbtype"); }  
        }
  if (getValue("sshHost")!=undefined){  this.form.sshHost=getValue("sshHost"); }
 if (getValue("sshPort")!=undefined){  this.form.sshPort=getValue("sshPort"); }
 if (getValue("sshUser")!=undefined){  this.form.sshUser=getValue("sshUser"); }
  if (getValue("sshPass")!=undefined){  this.form.sshPass=getValue("sshPass"); }
  if (getValue("dbpassword")!=undefined){ this.form.password=getValue("dbpassword");  }
        },
         methods: {
          getlan(name){ var str=this.$t('lang.'+name+''); if(str.indexOf("lang.")>=0){ return name; }return str },
       getadImgUrl(){
  var url="http://"+getValue('ip')+":8081/invoicing/images/ad.png";
           return url;
        },
                startLoading() {    //使用Element loading-start 方法
    loading = Loading.service({
        lock: true,
        text:"Uploading...",
         target: document.querySelector('.upload')//设置加载动画区域
    })
},
endLoading() {    //使用Element loading-close 方法
    loading.close()
    
},

        reload(){
                 window.location.reload(); 
        },
        showinvoice(){
            window.open('https://itunes.apple.com/cn/app/id1372437743?mt=12');
        },  showinvoice2(){
             window.open('https://itunes.apple.com/cn/app/id643680862?mt=12');
        },
        gotopage(pagename){
            this.$router.push({name:pagename});
        },
            selectdbtype(val){
              this.form.dbtype =val
              if(val=="postgres"){
               this.form.port="5432"
               this.form.username="postgres"
              } else if(val=="mysql"){
                 this.form.port="3306"
                 this.form.username="root"
              }else  if(val=="mssql"){
               this.form.port="1443"
               this.form.username="sa"
              }else  if(val=="etcd"){
               this.form.port="2379"
               this.form.username=""
              }else  if(val=="mongodb"){
               this.form.port="27017"
               this.form.username=""
              }
            },
goback(){
  if(getValue("userid")==undefined){
   this.$router.push({name:'login'});
}else{
  	 this.$router.push({name:'help'});
}

          },
       
           demo(){

              var  mapp={ 
                dbtype:"sqlite",
                table:'connectdb',
                dbname:"db.sqlite",
              }
            this.errorMessage="Loading..."
            GetList(Qs.stringify(mapp)).then(res=>{
                   if(res.data.code === 1){
                    setValue("dbtype","sqlite")
                     setValue("token",res.data.token)
                  // this.alert_ok(this.getlan('success'))
                 	 this.$router.push({name:'database'});
                }else{
                   this.alert_fail(res.data.message)
                   this.errorMessage=res.data.message
                }
            })

           },
          testconnect(){

var message="Connect"
//message=this.getlan('Connect_Postgresql_database_data_will_store_in_Postgresql')
if(this.form.dbtype.length>2&&this.form.ip.length>6&&this.form.port.length>0)
{
 
}else{
  this.alert_ok("Please input correct info");
  return
}
if(this.form.dbtype!="etcd"){
  if(this.form.username==""||this.form.password==""){
    this.alert_fail("please input username and password")
    return
  }
}
  setValue("dbip",this.form.ip)
  
this.errorMessage="Loading..."
            this.$confirm(message, "", {
		        confirmButtonText: this.getlan('Connect'),
		        cancelButtonText: this.getlan('cancel'),
		        type: 'warning'
	        }).then(() => {
	        	ConnectDatabase(this.form).then(res=>{
           // console.log(res)
            if(res.data.code === 1){
            
                setValue("dbip",this.form.ip)
                 setValue("dbport",this.form.port)
                  setValue("dbusername",this.form.username)
                   setValue("dbpassword",this.form.password)
                   setValue("dbname",this.form.dbname)
                    setValue("dbtype",this.form.dbtype)
                     setValue("token",res.data.token)

                     setValue("sshHost",this.form.sshHost)
                    setValue("sshPort",this.form.sshPort)
                    setValue("sshUser",this.form.sshUser)
                   setValue("sshPass",this.form.sshPass)
                   
                   this.alert_ok(this.getlan('success goto db'))
                   if(this.form.dbtype=="etcd"){
                     this.$router.push({name:'etcd'});
                   }else{
                     this.$router.push({name:'database'});
                   }
                 	 
                }else{
                   this.alert_fail(res.data.message)
                   this.errorMessage=res.data.message
                }
             })
	        }).catch(() => {
	        
          });
          

 
          },
         changeLangEvent: function () {
    console.log('changeLangEvent');
    this.$confirm('Change Language?', 'tips', {
      confirmButtonText: 'OK',
      cancelButtonText: 'Cancel',
      type: 'warning'
    }).then(() => {
      if ( this.$i18n.locale === 'zh-CN' ) {
        this.$i18n.locale = 'en-US';//关键语句  2.23  3.5  3.8  4.5  4.25  5.16
        console.log('en-US')
      }else {
        this.$i18n.locale = 'zh-CN';//关键语句
        console.log('zh-CN')
      }
    }).catch(() => {
      console.log('catch');
      this.$message({
        type: 'info',
      });
    });
  },
   downloadSqlite(){
          ///invoicing/
             var dbname=getValue("filename")
             var host= window.location.host
             host=window.location.host.replace("8080","8082")
             var url='http://127.0.0.1:8082/db/'+dbname
      
              // window.location.href = 'http://'+window.location.host+'/db/download?filetype=sqlite';
var params={url:url}
 openurl(Qs.stringify(params)).then(res=>{ })
 //alert_ok("To download you can open this url "+url)
 this.errorMessage="Download "+url;

/*
          var BrowerName=getBrowerName();

          if(BrowerName=="Chrome"||BrowerName=="Safari"||BrowerName=="Firefox"||BrowerName=="IE"||BrowerName=="Opera"||BrowerName=="FF"){
        
           window.open(url, '_blank'); 
          }else{
this.alert_ok(this.getlan('PleaseUseInWebBrowser')+url)

 window.open(url, '_blank'); 
          }
          */getValue

          },
         uploadSqlite(){
           
           if(this.fileList.length==0){
             this.alert_ok( this.getlan('Please_select_a_Sqlite_file'))
             return
            }

            if(this.fileList.length>1){
             this.alert_ok( this.getlan('Please_select_only_one_Sqlite_file'))
             return
            }

   var formData = new FormData()
    this.fileList.forEach(file => { 
       formData.append('dbname', file.raw.name);
       this.form.dbname=file.raw.name;
　　　　　formData.append('reportFile', file.raw, file.raw.name); //此处一定是append file.raw 上传文件只需维护fileList file.raw.name要加上 
　　}) 
    //this.alert_ok(this.form.dbname)
　      formData.append('table', "uploadsqlite");
          this.startLoading()
           GetList(formData).then(res=>{
             this.endLoading()
              if(res.data.code==1){
                this.dialogServer = false;
             
                this.$refs.upload.clearFiles()
    
                 setValue("dbname",this.form.dbname)
                    setValue("dbtype","sqlite")
                     setValue("token",res.data.token)
                   //this.alert_ok(this.getlan('success'))
                    this.$router.push({name:'database'});

                //this.$refs['form'].resetFields();
              }else{
                this.$message.error(res.data.message);
              }
            })


          },
           //上传
              beforeAvatarUpload(file) {

                let Xls = file.name.split('.');

                if(Xls[1] === 'sqlite'||Xls[1] === 'db'||Xls[1] === 'Sqlite'){
                    return file
                }else {
                    this.$message.error('need   .sqlite')
                    return false
                }

            },
            onChange(file, fileList) {
  
            if(fileList.length>1){
           　fileList.pop()
             this.$message.error('Only one file');
             return;
            }
　　if (file.name.indexOf("db")<0&&file.name.indexOf("sqlite")<0&&file.name.indexOf("DB")<0&&file.name.indexOf("Sqlite")<0) {
　　　　this.$message.error('need  sqlite or db file');
　　　　fileList.pop()
     return;
　　}
　　let existFile = fileList.slice(0, fileList.length - 1).find(f => f.name === file.name)
　　if (existFile) {
　　　　this.$message.error('File exists');
　　　　fileList.pop()
　　}
　　this.fileList = fileList
setValue("filename",file.name)
     this.uploadSqlite()
},

        handleRemove(file, fileList) {
          this.fileList.fileList
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(`limit ${files.length} but ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(`remove ${ file.name }？`);
        },
        //上传成功返回列表
        handleSuccess (res,_file) {
         
            this.endLoading();
            if(res.data.code==1){
                this.dialogServer = false;
           
                  this.$refs.upload.clearFiles();// 上传成功后删除列表
          
                  
                    
              }else{
                
                this.$message.error(res.data.message);
              }

        },
   tabLangClick(value){
    // this.alert_ok(value)
          this.$i18n.locale = value;
setValue("lan",value)

   },
    tabSetClick(value){
    // this.alert_ok(value)
      
setValue("productset",value)

   },
          },
           checkedChange(){
            console.log(this.checked)
        },
               

getImgUrl(){
  var url="http://"+getReqUrl()+"/Picture/qrcode.png"
  //this.alert_ok(url)
           return url
        },
 doprint(){
    this.$htmlToPaper('printMe');
          },
        showandroid(){
 window.open('http://www.iosbuy.com/app/AndroidInvoiceOnline.htm');
          },
         showios(){
window.open('https://itunes.apple.com/cn/app/elink-invoice-online-for-purchase/id634263772?l=zh');
          },
        touchstart:function(e){
            var that = this;
            var list = that.sudokus;
            for (var i = 0, len = list.length; i < len; ++i) {
                if (list[i].id == e) {
                    that.curSelect = i;
                }
            }
        },touchend:function(){
            var that = this;
            that.curSelect = null;
        }
    
};  
</script>  
<style >  
 .el-main {
    margin: 0px 0 0 160px;
    padding: 10px;
  }
    .sudoku_row{
        display: flex;
        align-items: center;
        width:100%;
        flex-wrap: wrap;
    }
    .img_item{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width:95%;
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .opacity{
        opacity: 0.4;
        background: #e5e5e5;
    }
    .img_item img{
        margin-bottom: 3px;
        display: block;
    }
     .tlabel {
     float: left;margin-right:5px;
     width:100px;
     text-align:left;
      line-height:40px;
     
      height:30px;
     word-break:normal; display:block; white-space:pre-wrap;word-wrap : break-word ;overflow: hidden ;
    }
    .el-select{
      float: left;width:150px;
    }
    .el-input {
     float: left;width:150px;margin-left: 2px;
    }

</style>




